@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,300,700);
@import url(https://fonts.googleapis.com/css?family=Lato:400,300,700);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ebebeb;
}

h1,
h2,
h3 {
  font-family: "Shadows Into Light", cursive;
}

label {
  font-family: "Shadows Into Light", cursive;
}

.button {
  color: #fff;
  text-decoration: none;
  background: #395756;
  padding: 10px;
  border-radius: 50px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.header-container {
  max-width: 100%;
  padding: 8px;
  margin-bottom: 20px;
  background-color: #4f5d75;
  color: #edeef1;
  box-shadow: -4px 7px 9px 1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: -4px 7px 9px 1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: -4px 7px 9px 1px rgba(0, 0, 0, 0.18);

  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.one {
  grid-column: 1/3;
  margin-left: 5%;
  font-size: 28px;
}

.three {
  grid-column: 3/3;
  margin-right: 5%;
  margin-left: 55%;
}

.search-bar-container {
  margin: 10px 50px;
}

.search-bar-container label {
  margin-right: 10px;
}

.movies-list ul {
  list-style: none;
}

.label {
  font-size: 28px;
}

.movies-list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 5px;
  padding: 5px;
}

form {
  display: flex;
  flex-direction: column;
}

form input {
  width: 30%;
  padding: 5px;
  margin-top: 8px;
}

form button {
  margin-left: 25px;
}

.no-image {
  padding: 8px 15px;
}

.no-image p {
  margin-top: 8px;
}

.movie-card {
  margin: 50px auto;
  font-family: "Lato", Arial, sans-serif;
  position: relative;
  overflow: hidden;
  width: 300px;
  height: 520px;
  background: #fff;

  border-radius: 3px;
  box-shadow: 0 0 10px rgb(90, 89, 89);
}

.poster {
  width: 100%;
  height: 360px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 0 0 13px 13px;
}

.movie-overview {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.bottom-card-container {
  padding: 7px;
  color: #374151;
}

.bottom-card-container h3,
h5 {
  margin-bottom: 5px;
}

.more-button {
  padding: 4px 10px;
  border-radius: 20%;
  border-style: none;
  background-color: #4f5d75;
  color: #edeef1;
  position: absolute;
  cursor: pointer;
  right: 0;
  bottom: 0;
  margin-right: 5px;
  margin-bottom: 5px;
}

.more-button:hover {
  opacity: 0.8;
}

.link-container {
  margin-top: 20px;
}

.button {
  color: #fff;
  text-decoration: none;
  background: #395756;
  padding: 10px;
  border-radius: 50px;
  display: inline-block;
  border: none;
  transition: all 0.4s ease 0s;
}

.link:hover {
  color: #395756;
  background-color: #fff;
}

/* No Image CSS */
.no-image-container {
  font-family: "Lato", Arial, sans-serif;
  margin: 0 20px 50px 20px;
  text-align: center;
}

.no-image-container h3 {
  margin-bottom: 10px;
}

.genre-container2 {
  margin: 1%;
}

.genre-container2 ul {
  list-style: none;
}

.movie-info-container2 {
  margin: 1%;
}
/* Background Image CSS */

.single-container {
  font-family: "Lato", Arial, sans-serif;
}

.background-container {
  margin: 0 10px 50px 10px;
  position: relative;
  text-align: center;
  color: white;
}

.background-pic {
  max-height: 90%;
  max-width: 90%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 1%;
  opacity: 0.85;

  display: block;
  margin-left: auto;
  margin-right: auto;
}

.title-container {
  position: absolute;
  top: 70%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: 600;
  font-size: 280%;

  display: grid;
  grid-template-columns: auto;
}

.title-container h3 {
  grid-column-start: 1;
  grid-column-end: 3;
}

.title-container-secondary {
  margin-top: 35px;
}

.title-container p {
  font-size: 50%;
  font-weight: 400;
  grid-column-start: 1;
  grid-column-end: 3;
}

.genre-container {
  font-size: 50%;
  width: 120%;
  font-weight: 400;
  margin-top: 5px;

  display: grid;
  grid-template-columns: auto auto auto auto;
  text-align: left;
}

.genre-container ul {
  list-style: none;
}

.movie-info-container {
  margin: -3% 7% 3% 7%;
  padding: 5px;
}

.movie-info-container h5 {
  font-weight: 200;
  font-size: large;
}

.movie-info-container h5 {
  margin-bottom: 5px;
  font-weight: 500;
}

.button-container {
  margin-left: 7%;
}

.coming-soon-container {
  margin: 1% 5% 3% 5%;
  text-align: center;
}

@media screen and (min-width: 320px) and (max-width: 740px) {
  .title-container {
    font-weight: 200;
    font-size: 90%;
  }

  .title-container-secondary {
    margin-top: 15px;
  }
}

.title {
  color: blue;
}

.rec-image-container {
  margin: 2%;
  padding: 1%;
  display: grid;
  grid-template-columns: auto auto auto auto;
}

.rec-movies-list {
  margin: 5px;
  padding: 5px;
}

.rec-movies-list img {
  border-radius: 5%;
}

.now-playing-container {
  margin: 100px 50px;
}

.card-container {
  display: grid;
  grid-template-columns: auto auto auto;
}

.now-playing-card {
  height: auto;
  width: 225px;
  margin: 5px;
  padding: 1px;
}

.image {
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
}

/* slideshow */

.slideshow-container {
  max-width: 100%;
  position: relative;
  margin: auto;
}

.slides {
  display: none;
}

.previous .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 5px;
  color: white;
}

